import { readUserSession } from "@/lib/server/actions";

export default async function Navbar() {
  const sessionData = await readUserSession();

  if (!sessionData) {
    return <p>Ingen adgang</p>;
  }

  const { user, role, member } = sessionData;

  return (
    <nav>
      <h1>Velkommen, {member.name || "Bruger"}!</h1>
      <ul>
        <li>
          <a href="/admin">Admin Dashboard</a>
        </li>
        {role === "admin" && (
          <li>
            <a href="/admin/settings">Indstillinger</a>
          </li>
        )}
      </ul>
    </nav>
  );
}
